<template>
  <div class="music-list">
    <div class="back" @click="back">
      <i class="icon-back"></i>
    </div>
    <h1 class="title" v-html="title"></h1>
    <div class="bg-image" :style="bgStyle" ref="bgImage">
      <div class="play-wrapper" v-show="songs.length>0" ref="playBtn">
        <div class="play">
          <i class="icon-play"></i>
          <span class="text">随机播放曲目</span>
        </div>
      </div>
      <div class="filter" ref="filter"></div>
    </div>
    <div class="bg-layer" ref="layer"></div>
    <Scroll @scroll="scroll" :probe-type="probeType" :listen-scroll="listenScroll" :data="songs" class="list" ref="list">
      <div class="song-list-wrapper">
        <song-list @select="selectItem" :songs="songs"></song-list>
      </div>
      <div class="loading-container" v-show="!songs.length">
        <Loading></Loading>
      </div>
    </Scroll>
  </div>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import songList from 'base/song-list/song-list'
  import Loading from 'base/loading/loading'
  import {profixStyle} from 'common/js/dom'
  import {mapActions} from 'vuex'

  const RESIZE_HEIGHT = 40
  const transform = profixStyle('transform')

  export default {
    data() {
      return {
        //维护一个纵向滚动的距离
        scrollY: 0
      }
    },
    props: {
      bgImage: {
        type: String,
        default: ''
      },
      songs: {
        type: Array,
        default: []
      },
      title: {
        type: String,
        default: ''
      }
    },
    mounted() {
      //缓存这个bgImage的高度
      this.imageHeight = this.$refs.bgImage.clientHeight
      //最少滚动距离
      this.minTranslate = -this.imageHeight+RESIZE_HEIGHT
      this.$refs.list.$el.style.top = `${this.imageHeight}px`
    },
    computed: {
      bgStyle() {
        return `background-image: url(${this.bgImage})`
      }
    },
    created() {
      //初始化滚动模式，并且监听滚动
      this.probeType = 3
      this.listenScroll = true
    },
    methods: {
      back() {
        this.$router.back()
      },
      scroll(pos) {
        this.scrollY = pos.y
      },
      selectItem(song, index) {
        this.selectPlay({
          list: this.songs,
          index
        })
      },
      //actions调用
      ...mapActions([
        'selectPlay'
      ])
    },
    components: {
      Scroll,
      songList,
      Loading
    },
    watch: {
      //观测这个Y轴的偏移量
      scrollY(newY) {
        let zIndex = 0
        let scale = 1
        let maxLayerTranslate = Math.max(this.minTranslate,newY)
        this.$refs.layer.style[transform] = `translate3d(0,${maxLayerTranslate}px,0)`
        //this.$refs.layer.style.webkitTransform = `translate3d(0,${maxLayerTranslate}px,0)`
        const percent = Math.abs(newY / this.imageHeight)
        if(newY>0) {
          scale = 1 + percent
          zIndex = 10
        }
        if(newY < this.minTranslate) {
          zIndex = 10
          this.$refs.bgImage.style.paddingTop = 0
          this.$refs.bgImage.style.height = `${RESIZE_HEIGHT}px`
          this.$refs.playBtn.style.display = 'none'
        }else {
          this.$refs.bgImage.style.paddingTop = '70%'
          this.$refs.bgImage.style.height = 0
          this.$refs.playBtn.style.display = 'block'
        }
        this.$refs.bgImage.style.zIndex = zIndex
        this.$refs.bgImage.style[transform] = `scale(${scale})`
        //this.$refs.bgImage.style.webkitTransform = `scale(${scale})`
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 0
      width: 100%
      /*overflow: hidden*/
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



